*{
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
}
body{
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
}
p{
  margin-top: 0;
  margin-bottom: 1rem;
}
a, a:hover{
  text-decoration: none;
}
a:visited{
  color: inherit;
}
img, svg{
  vertical-align: middle;
}
h1, h2, h3, h4 h5, h6{
  font-weight: 500;
}
.image-preload{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ecf0ff;
}
.image-preload--gallery-preview{
  min-width: 144px;
  min-height: 81px;
}
.image-preload--movie-poster{
  min-width: 290px;
  min-height: 163px;
}
.hidden {
  display: none;
}

@media (min-width: 800px) {
  .image-preload--gallery-preview{
    min-width: 110px;
    min-height: 62px;
  }
  .image-preload--movie-poster{
    min-width: 230px;
    min-height: 345px;
  }
}
@media (min-width: 1300px) {
  .image-preload--gallery-preview{
    min-width: 182px;
    min-height: 102px;
  }
  .image-preload--movie-poster{
    min-width: 346px;
    min-height: 518px;
  }
}
.Header{
  width: 100%;
  z-index: 10;
  color: #ffffff;
  background-color: #38517d;
}
.Header__home{
  display: block;
  margin: 0 0 5px 0;
  font-size: 30px;
  text-align: center;
  transition: 0.2s;
  padding-bottom: 10px;
  border-bottom: 2px solid #4071a5;
  width: 100%;
  text-decoration: none;
  color: #ffffff;
}
.Header__home:hover{
  border-bottom: 2px solid white;
  color: #ffffff;
  text-decoration: none;
}
.Header__home:active{
  opacity: 0.6;
}
.Header__additional{
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.Header__btn{
  position: relative;
  padding: 10px;
  margin: 5px 0;
  transition: 0.2s;
  background-color: #4c8ecd;
  text-align: center;
  font-size: 20px;
  color: #ffffff;
  border: 1px solid #3f5487;
  text-transform: uppercase;
}
.Header__btn:hover{
  background-color: #4c84c3;
  border: 1px solid #41669b;
}
.Header__btn:active{
  background-color: #3d6191;
  border: 1px solid #41669b;
}
.Header__btn--active{
  background-color: #3d6191;
}
.Header__btn--active:hover{
  background-color: #3a5987;
  border: 1px solid #41669b;
}
.Header__options{
  order: 2;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.Header__sidebar{
  order: 3;
}
.Header__favorites{
  order: 4;
  padding: 0;
}
.Header__favorites a{
  display: block;
  width: 100%;
  padding: 10px;
}
.Header__favorites-nonbtn{
  padding: 10px 0;
}
.Header__favorites-nonbtn:hover{
  background-color: #405c91;
}
.Header__back a{
  display: block;
}
.Header__search{
  order: 1;
  flex-grow: 2;
  margin: 10px 0;
}
.Header__search input{
  box-sizing: border-box;
  border: 2px solid white;
  border-radius: 3px;
  padding: 5px 10px;
  width: 100%;
  outline: none;
  transition: 0.5s;
}
.Header__search input:focus{
  border: 2px solid #b8d6ff;
  border-radius: 7px;
}

@media (min-width: 800px) {
  .Header h1{
    margin: 0;
  }
  .Header__home{
    width: auto;
    padding-bottom: 5px;
    background: linear-gradient(to bottom, #4071a5 5%, #4283b9 50%, #4071a5 95%);
  }
  .Header__home:hover{
    color: #dadeeb;
    border-bottom: 2px solid #4071a5;
    background: linear-gradient(to bottom, #4071a5 20%, #4283b9 50%, #4071a5 80%);
  }
  .Header__additional{
    flex-direction: row;
    position: relative;
  }
  .Header__btn{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 125px;
    height: 60px;
    position: relative;
    padding: 0;
    margin: 0;
    border: none;
    transition: 0.2s;
    background-color: inherit;
  }
  .Header__btn:hover{
    background-color: #41669b;
    border: none;
  }
  .Header__btn--active{
    background-color: #405c91;
    margin-bottom: 295px;
  }
  .Header__btn--active:hover{
    background-color: #406196;
  }
  .Header__search{
    order: 4;
    flex-grow: 2;
    background-color: transparent;
    padding: 0;
    margin: 10px;
    height: 40px;
  }
  .Header__search-input-wrapper{
    height: 100%;
  }
  .Header__search input{
    height: 100%;
  }
  .Header__search input:focus{
    border-radius: 5px;
  }
  .Header__back{
    width: 177px;
    margin-right: 10px;
  }
  .Header__back a{
    padding: 0;
    margin: 0;
  }
  .Header__favorites-nonbtn{
    width: 260px;
    margin-bottom: 0;
  }
  .Header__favorites-nonbtn:hover{
    background-color: #405c91;
  }
  .Header__options{
    order: 1;
    padding-right: 10px;
    position: relative;
  }
  .Header__options span{
    padding-left: 10px;
  }
  .Header__sidebar{
    position: absolute;
    left: 0;
    top: 65px;
    order: 2
  }
  .Header__favorites{
    order: 3;
  }
  .Header__favorites--moviePage{
    width: 250px;
  }
  .Header__favorites--movieDetails{
    width: 167px;
  }
  .Header__additional--movie{
    position: relative;
  }
}

@media (min-width: 1300px) {
  .Header__home {
    font-size: 44px;
  }
  .Header__additional{
    font-size: 24px;
  }
  .Header__sidebar{
    top: 85px;
  }
  .Header__btn{
    height: 80px;
    width: 200px;
    font-size: 26px;
  }
  .Header__btn:active{
    opacity: 0.8;
  }
  .Header__options, .Header__back{
    margin-right: 15px;
  }
  .Header__search{
    height: 50px;
    margin: 15px;
  }
  .Header__search input{
    padding-left: 15px;
    display: flex;
    align-self: stretch;
    font-size: 24px;
  }
  .Header__search input:focus{
    border-radius: 10px;
  }
  .Header__options a, .Header__favorites a, .Header__back a {
    padding: 0;
  }
  .Header__btn--active{
    margin-bottom: 425px;
  }
  .Header__back{
    width: 278px;
  }
  .Header__favorites--moviePage{
    width: 375px;
  }
  .Header__favorites--movieDetails{
    width: 268px;
  }
  .Header__favorites-nonbtn{
    width: 395px;
    margin-bottom: 0;
  }
  .Header__favorites-nonbtn:active{
    opacity: 1;
  }
}
.Sidebar{
  width: 100%;
  margin: -5px 0 -5px 0;
  background: linear-gradient(to left, #4071a5 5%, #4283b9 50%, #4071a5 95%);
  padding: 5px 0;
}
.Sidebar__wrapper{
  display: flex;
  flex-direction: column;
  padding: 0 2px;
}
.Sidebar__header{
  font-size: 16px;
  margin: 0;
  text-align: left;
  padding-bottom: 3px;
}
.Sidebar__genreList-wrapper{
  display: flex;
  flex-wrap: wrap;
  padding-top: 3px;
  justify-content: space-around;
}
.Sidebar__genre-label{
  display: flex;
  width: 32%;
  align-items: center;
  cursor: pointer;
  transition: 0.2s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-bottom: 0.5em;
}
.Sidebar__genre-label:hover{
  color: #d3dbe1;
}
.Sidebar__genre-input{
  display: none;
}
.Sidebar__genre-custom{
  box-sizing: border-box;
  display: flex;
  position: relative;
  width: 12px;
  height: 12px;
  border: 1px solid white;
  margin-right: 8px;
  z-index: 0;
}
.Sidebar__genre-input:checked + .Sidebar__genre-custom{
  border: none;
}
.Sidebar__genre-input:checked + .Sidebar__genre-custom::after{
  display: block;
  position: absolute;
  top: -1px;
  left: 0;
  content: "";
  background: url('data:image/svg+xml;base64,');
  background-size: cover;
  width: 14px;
  height: 14px;
  z-index: 2;
}
.Sidebar__genre-name{
  font-size: 11px;
}
.Sidebar__sort-params{
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}
.Sidebar__sort-wrapper{
  display: flex;
  flex-direction: column;
  padding-top: 3px;
}
.Sidebar__sort-label{
  display: flex;
  border-radius: 8px;
  margin: 0;
  cursor: pointer;
  transition: 0.2s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-bottom: 0.2em;
}
.Sidebar__sort-label:hover{
  color: #d3dbe1;
}
.Sidebar__sort-radiobtn{
  display: flex;
  align-items: center;
}
.Sidebar__sort-input{
  display: none;
}
.Sidebar__sort-custom{
  box-sizing: border-box;
  display: flex;
  width: 13px;
  height: 13px;
  border: 2px solid white;
  border-radius: 10px;
  margin-right: 8px;
}

.Sidebar__sort-input:checked + .Sidebar__sort-custom::after{
  display: block;
  content: "";
  width: 5px;
  height: 5px;
  background: white;
  border-radius: 10px;
  margin: auto;
}
.Sidebar__sort-name{
  font-size: 11px;
}
.Sidebar__wrapper--hidden{
  display: none;
}
@media (min-width: 800px) {
  .Sidebar{
    padding: 10px;
  }
  .Sidebar__wrapper{
    flex-direction: row;
    flex-wrap: wrap;
    padding-bottom: 10px;
  }
  .Sidebar__header{
    margin: 5px 0;
  }
  .Sidebar__header-text{
    font-size: 18px;
    font-weight: bold;
  }
  .Sidebar__genreList-wrapper{
    margin: 0 5px;
    justify-content: flex-start;
  }
  .Sidebar__genre-label{
    width: 18%;
    margin-right: 13px;
  }
  .Sidebar__genre-name{
    font-size: 15px;
  }
  .Sidebar__sort-wrapper{
    flex-direction: row;
    justify-content: space-between;
    margin-right: -50px;
    width: 100%;
  }
  .Sidebar__sort-name{
    font-size: 16px;
    margin-right: 10px;
  }
  .Sidebar__sort{
    display: flex;
    flex-direction: column;
    flex-grow: 2;
  }
  .Sidebar__sort-custom{
    margin-right: 5px;
  }
  .Sidebar__sort:last-child{
    width: 160px;
    flex-grow: 1;
  }
  .Sidebar__sort-params{
    display: flex;
    width: 100%;
  }
  .Sidebar__sort-label{
    border-radius: 4px;
    align-items: baseline;
    width: 25%;
  }
  .Sidebar__sort-wrapper--direction{
    flex-direction: column;
    justify-content: space-between;
    padding-left: 10px;
  }
  .Sidebar__sort-wrapper--direction label{
    width: 100%;
  }
}

@media (min-width: 1300px) {
  .Sidebar__header{
    margin-top: 15px;
  }
  .Sidebar__header-text{
    font-size: 26px;
  }
  .Sidebar__genreList-wrapper{
    margin-left: 15px;
  }
  .Sidebar__genre-label{
    margin-right: 20px;
  }
  .Sidebar__genre-custom{
    width: 18px;
    height: 18px;
    margin-right: 10px;
  }

  .Sidebar__genre-input:checked + .Sidebar__genre-custom{
    border: none;
  }
  .Sidebar__genre-input:checked + .Sidebar__genre-custom::after{
    width: 21px;
    height: 22px;
  }
  .Sidebar__genre-name{
    font-size: 22px;
  }
  .Sidebar__sort:last-child{
    width: 255px;
  }
  .Sidebar__sort-name{
    font-size: 22px;
    margin-right: 20px;
  }
  .Sidebar__sort-custom{
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
  .Sidebar__sort-input:checked + .Sidebar__sort-custom::after{
    width: 12px;
    height: 12px;
  }
  .Sidebar__sort-wrapper{
    margin: 0;
    padding-left: 15px;
    justify-content: space-between;
  }
  .Sidebar__sort-wrapper--direction{
    margin: 0;
  }
}
.Movie{
  display: flex;
  width: 130px;
  position: relative;
  flex-direction: column;
  margin: 10px auto;
  background-color: white;
  box-shadow: 0 0 10px 0 rgba(0,0,0,0.75);
  border-radius: 5px;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.2s;
}
