.FaveBtn{
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 4px;
  left: 4px;
  background-color: #28a745;
  border-radius: 5px;
  border: 1px solid transparent;
  width: calc(100% - 8px);
  height: 25px;
  padding: 0;
  transition: 0.2s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
.FaveBtn:hover{
  background-color: #24873f;
}
.FaveBtn__content{
  display: flex;
  align-items: center;
  width: 100%;
}
.FaveBtn__img{
  width: 14px;
  height: 14px;
  margin-left: 5px;
  margin-right: 15px;
}
.FaveBtn__star{
  fill: transparent;
  stroke: white;
}
.FaveBtn__star--active{
  fill: white;
  stroke: white;
}
.FaveBtn__text{
  color: white;
  font-size: 10px;
}
.FaveBtn--active{
  background-color: #c33240;
  transition: 0.2s;
}
