.ImageShow{
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  z-index: 6;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  overflow: auto;
}
.ImageShow__image-wrapper{
  position: relative;
  overflow: hidden;
  min-width: 290px;
  min-height: 163px;
  width: 100%;
}
.ImageShow__image-wrapper img{
  width: 100%;
}
.ImageShow__interface{
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 100%;
}
.ImageShow__interface button{
  outline: none;
}
.ImageShow__prev, .ImageShow__next{
  position: absolute;
  cursor: pointer;
  width: 26px;
  height: 50px;
  top: calc(50% - 15px);
  border: none;
  background-color: rgba(0, 0, 0, 0.65);
  opacity: 1;
  transition: 0.15s;
}
.ImageShow__prev:hover, .ImageShow__next:hover{
  background-color: rgba(0, 0, 0, 0.8);
}
.ImageShow__prev:active, .ImageShow__next:active{
  opacity: 0.5;
}
.ImageShow__prev{
  left: 0;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.ImageShow__next{
  right: 0;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.ImageShow__prev::before, .ImageShow__next::before,
.ImageShow__prev::after, .ImageShow__next::after{
  position: absolute;
  left: calc(50% - 10px);
  display: block;
  content: "";
  width: 20px;
  height: 2px;
  background-color: white;
}
.ImageShow__prev::before {
  top: calc(50% - 6px);
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.ImageShow__prev::after{
  top: calc(50% + 7px);
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.ImageShow__next::before {
  top: calc(50% - 6px);
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.ImageShow__next::after{
  top: calc(50% + 7px);
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.ImageShow__close{
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  border: none;
  background-color: rgba(0, 0, 0, 0.65);
  cursor: pointer;
  opacity: 1;
  transition: 0.15s;
}
.ImageShow__close:hover{
  background-color: rgba(0, 0, 0, 0.8);
}
.ImageShow__close:active{
  opacity: 0.5;
}
.ImageShow__close::before, .ImageShow__close::after{
  position: absolute;
  top: calc(50% - 1px);
  left: calc(50% - 7px);
  display: block;
  content: "";
  width: 15px;
  height: 2px;
  border-radius: 2px;
  background-color: white;
}
.ImageShow__close::before{
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.ImageShow__close::after{
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
@media (min-width: 800px) {
  .ImageShow__image-wrapper{
    min-width: 550px;
    min-height: 310px;
  }
}
@media (min-width: 1300px) {
  .ImageShow__image-wrapper{
    min-width: 1000px;
    min-height: 500px;
  }
  .ImageShow__prev, .ImageShow__next{
    width: 40px;
    height: 70px;
    top: calc(50% - 35px);
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 6px;
  }
  .ImageShow__prev{
    left: 20px;
  }
  .ImageShow__next{
    right: 20px;
  }
  .ImageShow__prev:hover, .ImageShow__next:hover{
    background-color: rgba(0, 0, 0, 0.7);
  }
  .ImageShow__close{
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, 0.6);
  }
  .ImageShow__close:hover{
    background-color: rgba(0, 0, 0, 0.7);
  }
  .ImageShow__close::before, .ImageShow__close::after{
    top: calc(50% - 2px);
    left: calc(50% - 10px);
    width: 20px;
    height: 2px;
  }
}
.lds-ring {
  display: block;
  position: relative;
  width: 64px;
  height: 64px;
  margin: auto;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 6px solid #4071a5;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #4071a5 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

